/* eslint-disable */
import { apiClient } from '../services/apiService'

export default {
  state: {
    questionsReports: [],
    answersReports: [],
    avialableTopics: []
  },
  getters: {
    getQuestionsReports (state) {
      return state.questionsReports
    },
    getAnswersReports (state) {
      return state.answersReports
    },
    getAvialableTopics (state) {
      return state.avialableTopics
    }
  },
  mutations: {
    IGNORE_REPORTS (state, { reportsType, id }) {
      if (reportsType === 'questionsReports') {
        let index = state.questionsReports.findIndex(item => item.id == id)
        state.questionsReports.splice(index, 1)
      }
      if (reportsType === 'answersReports') {
        let index = state.answersReports.findIndex(item => item.id === id)
        state.answersReports.splice(index, 1)
      }
    },
    GET_ALL_TOPICS (state, payload) {
      state.avialableTopics = payload
    },
    RETRIEVE_REPORTS (state, payload) {
      state.questionsReports = payload.reportedQuestions
      state.answersReports = payload.reportedAnswers
    },
    DELETE_QUESTION_BY_ADMIN (state, questionId) {
      let index = state.questionsReports.findIndex(item => item.id === questionId)
      state.questionsReports.splice(index, 1)
    },
    DELETE_ANSWER_BY_ADMIN (state, answerId) {
      let index = state.answersReports.findIndex(item => item.id === answerId)
      state.answersReports.splice(index, 1)
    }
  },
  actions: {
    reportQuestion ({ commit }, payload) {
      apiClient.post('/report', {
        id: payload.id,
        userId: payload.userId,
        type: 'question'
      })
        .then(response => {
          commit('SET_NOTIFICATION', {
            message: 'reported successfully',
            type: 'success'
          })
        })
        .catch(error => {
          commit('SET_NOTIFICATION', {
            message: error.response.data,
            type: 'error'
          })
        })
    },
    reportAnswer ({ commit }, payload) {
      apiClient.post('/report', {
        id: payload.id,
        userId: payload.userId,
        type: 'answer'
      })
        .then(response => {
          commit('SET_NOTIFICATION', {
            message: 'reported successfully',
            type: 'success'
          })
        })
        .catch(error => {
          commit('SET_NOTIFICATION', {
            message: 'sorry,something wrong',
            type: 'error'
          })
        })
    },
    retrieveReports ({ commit }) {
      apiClient.get('/reports')
        .then(response => {
          commit('RETRIEVE_REPORTS', response.data)
        })
        .catch(error => {
          console.log(error)
        })
    },
    deleteQuestionByAdmin ({ commit, dispatch }, { answerId, questionId, reportsType }) {
      //you should do if condition, if this request come from question report or
      //answer report to call appropriate mutation
      return new Promise((resolve, reject) => {
        apiClient.delete('questions/' + questionId)
          .then(response => {
            // dispatch('markReportAsRead',reportId)
            if (reportsType === 'questionsReports') commit('DELETE_QUESTION_BY_ADMIN', questionId)
            if (reportsType === 'answersReports') commit('DELETE_ANSWER_BY_ADMIN', answerId)
            commit('SET_NOTIFICATION', {
              message: response.data,
              type: 'success'
            })
            resolve(response)
          })
          .catch(error => {
            commit('SET_NOTIFICATION', {
              message: 'sorry,something wrong',
              type: 'error'
            })
            reject(error)
          })
      })
    },
    deleteAnswerByAdmin ({ commit, dispatch }, payload) {
      return new Promise((resolve, reject) => {
        apiClient.delete('answers/' + payload.answerId)
          .then(response => {
            // dispatch('ignoreReports',{id:answerId,reportsType:'answersReports'})
            // dispatch('markReportAsRead',payload.reportId)
            commit('DELETE_ANSWER_BY_ADMIN', payload.answerId)
            commit('SET_NOTIFICATION', {
              message: response.data,
              type: 'success'
            })
            resolve(response)
          })
          .catch((error) => {
            commit('SET_NOTIFICATION', {
              message: 'sorry,something wrong',
              type: 'error'
            })
            reject(error)
          })
      })
    },
    deleteUserByAdmin ({ commit, dispatch }, { reportedContentId, reportsType, userId }) {
      return new Promise((resolve, reject) => {
        apiClient.delete('user/' + userId)
          .then(response => {
            dispatch('ignoreReports', {
              id: reportedContentId,
              reportsType: reportsType
            })
            commit('SET_NOTIFICATION', {
              message: response.data,
              type: 'success'
            })
            resolve(response)
          })
          .catch(error => {
            commit('SET_NOTIFICATION', {
              message: 'sorry ,something goes wrong',
              type: 'error'
            })
            reject(error)
          })
      })
    },
    ignoreReports ({ commit }, { reportsType, id }) {
      let type1 = 'q'
      if (reportsType === 'questionsReports') type1 = 'question'
      if (reportsType === 'answersReports') type1 = 'answer'
      return new Promise((resolve, reject) => {
        apiClient.put('ignore_reports_on_' + type1, {
          id: id
        })
          .then(response => {
            commit('IGNORE_REPORTS', {
              reportsType: reportsType,
              id
            })
            commit('SET_NOTIFICATION', {
              message: 'operation done',
              type: 'success'
            })
            resolve(response)
          })
          .catch((error) => {
            commit('SET_NOTIFICATION', {
              message: 'sorry,something gone wrong',
              type: 'error'
            })
            reject(error)
          })
      })
    },
    getAllTopics ({ commit }) {
      // return new Promise((resolve, reject) => {
        apiClient.get('/admin/allTopics')
          .then(response => {
            commit('GET_ALL_TOPICS', response.data)
            // resolve(response)
          })
          .catch(error => {
            // reject(error)
          })
      // })
    },
    markReportAsRead ({ commit }, reportedContentId, reportsType) {
      apiClient.post('reports/markAsRead/' + reportId)
        .then((response) => {
        })
        .catch(error => {

        })
    }
  },
}
