<template>
  <v-content>
    <span v-html="this.$options.filters.firstThreeLines(text)"></span>
<!--    <v-btn v-if="(!showMore)&&(text.length>300)" @click="showMore=!showMore" outlined x-small>show more</v-btn>-->
<!--    <span v-if="showMore" >{{text|restLines}}</span>-->
<!--    <v-btn v-if="showMore" @click="showMore=!showMore" outlined x-small>show less</v-btn>-->
  </v-content>
</template>

<script>/* eslint-disable */
  export default {
    name: 'TruncatedText',
    props: {
      text: {
        type: String,
        required:true
      },
    },
    data () {
      return {
        showMore: false
      }
    },
    created () {
      var decodeHTML = function (html) {
        var txt = document.createElement('textarea');
        txt.innerHTML = html;
        return txt.value;
      };
      this.text= decodeHTML(this.text);
    }
  }
</script>

