<template>
  <v-card height="80%">
    <v-card-title class="primary white--text justify-center">
      <span>TOP CONTRIBUTORS</span>
    </v-card-title>
    <v-divider/>
    <v-card-title>
          <span class="mr-auto">NAME</span>
          <span class="ml-auto">POINTS</span>
    </v-card-title>
      <v-list max-height="60%" style="overflow-y: scroll;">
        <v-list-item v-for="(contributor,index) in getTopContributors"
                     :key="index" class="d-flex">
          <div class="mr-auto text-capitalize">
            {{contributor.first_name}} {{contributor.last_name}}
          </div>
          <div class="ml-auto">
            {{Math.round(contributor.points)}}
          </div>
        </v-list-item>
      </v-list>
  </v-card>
</template>

<script>/* eslint-disable */
    import {mapGetters} from 'vuex'
    export default {
        name: "TopContributors",
        computed:mapGetters(["getTopContributors"]),
        created() {
            this.$store.dispatch('retrieveTopContributors')
        }
    }
</script>

