<template>
  <v-row class="d-flex justify-center">
    <v-col cols="10">
      <v-row>
        <v-col cols="12" md="10">
          <app-question :question="question"/>
          <v-divider/>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols='10' md="8">
          <template v-for="(answer,index) in question.answers">
            <div :key="index" class="my-6">
              <v-divider/>
              <app-answer :pressed-type="false" :answer="answer"/>
            </div>
          </template>
        </v-col>
      </v-row>
    </v-col>
  </v-row>

</template>

<script>/* eslint-disable */

import Question from "./home/Question";
import Answer from "./home/Answer";

export default {
    name: "QuestionAnswers",
    components: {
        AppQuestion: Question,
        AppAnswer: Answer
    },
    computed: {
        question() {
            // return this.$store.getters.getQuestion(this.$route.params.id);

            let res = this.$store.getters.getAllQuestions
                .find(item => item.id == this.paramsQuestionID)
            return res


        },

        paramsQuestionID() {

            return this.$route.params.id

        }

    },
    watch: {
        paramsQuestionID(newValue, oldValue) {
            this.$store.dispatch('retrieveQuestion', newValue)

        }
    },

    created() {
        // if (this.question.answers)
        this.$store.dispatch('retrieveQuestion', this.paramsQuestionID)

    },


}
</script>

