<template>
  <v-card flat>
    <v-card-title>
      {{stepTitles[currentStep-1]}}
      <v-spacer/>
      <v-btn icon text color="red" @click="cancel">x</v-btn>
    </v-card-title>
    <v-stepper v-model="currentStep">
      <v-stepper-items>

        <!--------------------------------------step 1--------------------------------->
        <v-stepper-content step="1">

          <v-card-text>
            <v-text-field
              ref="question"
              placeholder="start your question by What,Why,How..etc"
              v-model="askQuestion.body" :rules="[required]" autofocus
            ></v-text-field>
          </v-card-text>

          <v-card-actions>
            <v-spacer/>
            <v-btn class="mr-3" @click="cancel">
              Cancel
            </v-btn>
            <v-btn class="mr-3" @click="next" :loading="isFirstStepLoading">next</v-btn>
          </v-card-actions>
        </v-stepper-content>
        <!--------------------------------------step 2--------------------------------->
        <v-stepper-content step="2" >
          <v-card-text >
            your selections:
            <v-chip-group
              mobile-break-point="150"
            >
              <v-chip color="cyan"
                       label
                      close
                      text-color="white"
                       v-for="(topic,index) in askQuestion.topics" :key="index"
                      @click:close="deleteTopic()">
                {{ topic.title }}
              </v-chip>
            </v-chip-group>
            <v-row>
              <v-col background-color="grey" cols="12" sm="6" >
                <p>suggested by NaiveBayes</p>
                <template v-for="(item,index) in topics">
                    <v-checkbox   :key="index" multiple class="ma-0 pa-0"
                                v-model="askQuestion.topics" :label="item.title" :value="item">
                    </v-checkbox>
                </template>
              </v-col>
              <v-col cols="12" sm="6">
                <p>suggested by SVM</p>
                <template v-for="(item,index) in topics">
                  <v-checkbox :key="index" multiple class="ma-0 pa-0"
                              v-model="askQuestion.topics" :label="item.title" :value="item">
                  </v-checkbox>
                </template>
              </v-col>
            </v-row>
            <v-autocomplete
              v-model="askQuestion.topics"
              :search-input.sync="searchValue"
              item-text="title"
              item-value="id"
              :items="topicsFromSearch"
              chips
              multiple
              deletable-chips
              return-object
              hide-no-data
              hide-selected
              label="Add Topic"
              :loading="isLoading"
              @update:search-input="getAsyncData"
            >
            </v-autocomplete>
          </v-card-text>
          <v-card-actions>
            <v-spacer/>
            <v-btn class="mr-3" @click="back">
              back
            </v-btn>
            <v-btn class="mr-3" @click="submit">Ask</v-btn>
          </v-card-actions>
        </v-stepper-content>
<!--        --------------------------------------STEP 3-------------------------------->
        <v-stepper-content step="3">
          {{askQuestion}}
          <app-answer-request :question="askQuestion" @cancel="cancel"/>
        </v-stepper-content>

      </v-stepper-items>

    </v-stepper>

  </v-card>
</template>

<script>/* eslint-disable */
import {apiClient} from "../services/apiService";
import debounce from 'lodash/debounce'
import AnswerRequest from './layoutDialogs/AnswerRequest'

export default {

  name: 'AddQuestion',
  components :{
    appAnswerRequest:AnswerRequest
  },
  computed :{},
  data() {
    return {
      isFirstStepLoading:false,
      stepTitles:['Ask Question',"Select Topics","Possible Answers"],
      currentStep: 1,
      topics: [],
      topicsFromSearch:[],
      askQuestion: {
        body: '',
        topics: []
      },
      searchValue: '',
      isLoading: false
    }
  },
  methods: {
    required(value) {
      return !!value || 'this field is required'
    },
    next(){
      if(!this.$refs['question'].hasError) {
        if(this.currentStep===1){
          this.predictQuestionTopics()
          return
        }
        return this.currentStep++;
      }
    },
    back(){
      this.currentStep--;
    },
    cancel() {
      this.$emit('cancel')
    },
    seePossibleAnswers(){
      this.$emit('seePossibleAnswers',this.askQuestion.body);
    },
    predictQuestionTopics(){
      this.isFirstStepLoading=true
      this.$store.dispatch('predictQuestionTopics',this.askQuestion.body)
        .then(response=>{
          // console.log(response.data)
          this.topics.push(...response.data)
          this.currentStep++;
          this.isFirstStepLoading=false
        })
        .catch(error=>{
          this.isFirstStepLoading=false
        })
    },
    deleteTopic(topicId){
      let idx=this.askQuestion.topics.findIndex(item=>item.id===topicId)
      this.askQuestion.topics.splice(idx,1)
    },
    submit() {
      this.$store.dispatch('addQuestion', this.askQuestion)
        .then((response) => {
          // location.reload()
          this.$store.commit("SET_NOTIFICATION", {
            message: 'Your Question Added Successfully',
            type: 'success'
          })

          this.next()
          this.askQuestion = response.data
        })
        .catch((error) => {
          this.$store.commit("SET_NOTIFICATION", {
            message: error.response.data,
            type: 'error'
          })
        })
    },
    getAsyncData: debounce(function () {
        if (!!this.searchValue && this.searchValue.length > 2) {
          this.isLoading = true
          apiClient.get('/topics/search', {
            params: {
              searchValue: this.searchValue
            }
          })
            .then(response => {
              this.topicsFromSearch = response.data
              this.isLoading = false
            })
            .catch(() => {
              this.isLoading = false
            })
        }
      }, 1000)
  },
    destroyed() {
      this.$store.commit('CLEAR_USERS_IN_TOPIC')
    }

}
</script>

<style scoped>
  .listBorder {
    border-top-style: solid;
    border-left-style: solid;
    border-right-style: solid;
    border-bottom-style: solid;
    border-width: 1px;
    border-color: rgba(0, 0, 0, 0.1) !important;
  }
</style>
