<template>
  <v-card class="color-grad-light" flat>

    <v-row>
      <v-col md="3" class="hidden-sm-and-down">
        <app-topic-list/>
      </v-col>
      <v-col cols="12" md="6">
        <template v-for="question in getAllQuestions">
          <v-card outlined class="mb-3" :key="question.id">
            <app-extended-question :question="question"/>
          </v-card>
        </template>
        <v-card flat v-intersect="onIntersect">
          <v-skeleton-loader v-if="isLoading"
                             light
                             class="mx-auto"
                             type="card"
          />
        </v-card>
        <template v-if="isInitialLoading">
          <v-card v-for="i in 5" :key="i" class="mt-3">
            <v-skeleton-loader light
                               class="mx-auto"
                               type="card"
            />
          </v-card>
        </template>
      </v-col>
      <v-col class="hidden-sm-and-down" md="3">
        <app-unanswered-questions-carousel/>
      </v-col>

    </v-row>

  </v-card>

</template>

<script>/* eslint-disable */
import ExtendedQuestion from '../components/home/ExtendedQuestion'
import TopicList from '../components/topic/TopicList'
import {mapGetters} from 'vuex'
import UnAnsweredQuestionsCarousel from "../components/UnAnsweredQuestionsCarousel";

export default {
    inject: ['theme'],
    name: 'Home',
    components: {
        appExtendedQuestion: ExtendedQuestion,
        appTopicList: TopicList,
        appUnansweredQuestionsCarousel: UnAnsweredQuestionsCarousel
    },
    data() {
        return {
            isLoading: false,
            isInitialLoading: true
        }
    },
    methods: {
        onIntersect(entries, observer) {
            let isIntersecting = entries[0].isIntersecting
            if (isIntersecting) {
                this.isLoading = true
                this.$store.dispatch('retrieveAllQuestions')
                    .then(response => {
                        this.isLoading = false
                        this.isInitialLoading = false;
                    })
            }
        },
    },
    computed: {
        ...mapGetters([
            'getAllQuestions'
        ]),
    },
    created() {
        if (!this.$store.getters.getFromQuestionAnswersUrl) {
            this.$store.commit('CLEAR_QUESTIONS')
        }

    }

}
</script>
<style>
  .fixed-card{
    position: sticky;
    top: 50px;
  }
</style>
