<template>
  <v-card flat>
    <v-card-title>
      <v-tabs color="indigo" v-model="tab" style="border-bottom:solid #ccc 1px ">
        <v-tab v-for="(item,index) in items" :key="index">
          {{item.title}}
        </v-tab>
      </v-tabs>
    </v-card-title>
    <v-card-text class="pa-0">
      <v-tabs-items v-model="tab">
        <v-tab-item v-for="(item,index) in items" :key="index">
          <component :blocked="blocked" :is="item.component" :userId="userId"/>
        </v-tab-item>
      </v-tabs-items>
    </v-card-text>
  </v-card>
</template>

<script>/* eslint-disable */

import QuestionsTab from "./QuestionsTab";
import AnswersTab from "./AnswersTab";
import StatusTab from "./StatusTab";

export default {
    name: "Dashboard",
    props: {
        userId: {
            type: Number,
            required: true
        },
        blocked: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            tab: null,
            items: [
                {title: "Question", component: QuestionsTab},
                {title: "answer", component: AnswersTab},
                {title: "stats", component: StatusTab},
            ],
        }
    },
}
</script>

