<template>
  <v-card flat>
    <v-card-text style="height: min-content" class="py-0">
      <v-row v-if="question.topics.length>0"
             class="d-flex  justify-center align-center"
      >
        <v-col class="pa-0" cols="auto">
          <v-chip-group
            mobile-break-point="150"
          >
            <v-chip outlined small
                    color="red" :disabled="true"
                    v-for="(tag,index) in question.topics" :key="index">
              {{ tag.title }}
            </v-chip>
          </v-chip-group>
        </v-col>
      </v-row>
    </v-card-text>
    <v-card-title class="py-0 px-0">
      <v-list class="py-0" width="100%">
        <v-list-item>
          <v-list-item-avatar color="red">

            <v-img v-if="questionUser.image!=null"
                   :src="questionUser.image.url">
            </v-img>

            <span v-else class="white--text">
                {{questionUser.first_name.charAt(0).toUpperCase()}}
                {{questionUser.last_name.charAt(0).toUpperCase()}}
            </span>
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title>
              <router-link :to="{name:'profile',params: {id:questionUser.id}}">
                <span style="cursor: pointer"
                      @mouseenter.stop.exact="sendShowUserCardEvent"
                      @mouseleave.stop.exact="sendHideUserCardEvent"
                >
                  {{questionUser.first_name}} {{questionUser.last_name}}
                </span>
              </router-link>
              <span class="subtitle-2 font-weight-light grey--text">MAY-12-2017</span>
            </v-list-item-title>
            <v-list-item-subtitle>description</v-list-item-subtitle>
          </v-list-item-content>

          <v-list-item-action>
            <v-menu offset-y transition="scroll-y-transition"
                    v-if="activeOptionsCount > 0"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  v-bind="attrs"
                  v-on="on"
                  text
                  style="{font-size: 50px}"
                >
                  ...
                </v-btn>
              </template>
              <v-list>
                <template
                  v-for="(item, index) in listItems"
                >
                  <v-list-item
                    v-if="item.active"
                    :key="index"
                    @click="item.clicked"
                  >
                    <v-list-item-action-text>{{item.title}}</v-list-item-action-text>
                  </v-list-item>
                </template>
              </v-list>
            </v-menu>
          </v-list-item-action>
        </v-list-item>
      </v-list>
    </v-card-title>

    <div class="py-0 d-flex align-center justify-space-between">
      <!--      <-list-item-title class="ml-5 font-weight-bold">-->
      <router-link
        :to="{name:'questionAnswers',params:{id:question.id}}" class="pl-4 headline"
        tag="div" style="cursor: pointer;width: 60%"
      >
        {{question.body}}
      </router-link>
      <div class="d-flex justify-center" style="width: 40%">
        <div class="text-center mx-2 pink--text">
          <v-avatar style="border:1px pink solid"
                    class="mb-2 pink white--text"
          >
            {{question.answers_count}}
          </v-avatar>
          <div>Answers</div>
        </div>
        <div class="text-center mx-2 indigo--text">
          <v-avatar style="border:1px indigo solid"
                    class="mb-2 indigo--text"

          >
            {{question.votes_count}}
          </v-avatar>
          <div>Votes</div>
        </div>
      </div>
      <!--      </v-list-item-title>-->
    </div>

    <slot name="answer"/>
    <!--        <v-card-text class="headline font-weight-bold" >-->
    <!--          {{questionContent}}-->
    <!--        </v-card-text>-->
    <v-card-actions>
      <v-btn v-if="isNullOrUndefined(question.my_answer)" text color="indigo" @click="expand=!expand">
        <v-icon>mdi-pencil-box-outline</v-icon>
        Answer
      </v-btn>
      <v-btn else text color="indigo" @click="expand=!expand">
        <v-icon>mdi-pencil-box-outline</v-icon>
        See My Answer
      </v-btn>

      <v-tooltip top class="ml-2">
        <template v-slot:activator="{ on }">
          <v-btn v-on="on" class="ml-5"
                 text color="indigo"
                 @click="answerRequestDialog=true"
          >
            <v-icon>mdi-account-plus</v-icon>
            Request
          </v-btn>
        </template>
        <span>request someone to answer this question</span>
      </v-tooltip>

    </v-card-actions>
    <v-dialog :fullscreen="$vuetify.breakpoint.smAndDown"
              :transition="$vuetify.breakpoint.smAndDown?'dialog-bottom-transition':'dialog-transition'"
              v-model="editQuestionDialog" width="600px">
      <app-edit-question v-if="editQuestionDialog" @cancel="editQuestionDialog=false" :question="question"/>
    </v-dialog>
    <v-dialog :fullscreen="$vuetify.breakpoint.smAndDown"
              :transition="$vuetify.breakpoint.smAndDown?'dialog-bottom-transition':'dialog-transition'"
              v-model="answerRequestDialog" width="40vw" scrollable>

      <app-answer-request v-if="answerRequestDialog" :question="question"
                          @cancel="answerRequestDialog=false"
      />

    </v-dialog>
    <v-expand-transition>
      <v-card v-if="expand" flat>
        <v-card-text>
          <ckeditor :editor="editor" v-model="editorData"/>
        </v-card-text>

        <v-card-actions>
          <v-spacer/>
          <v-btn @click="addAnswer" outlined color="primary">submit</v-btn>
        </v-card-actions>
      </v-card>
    </v-expand-transition>
  </v-card>

</template>

<script>/* eslint-disable */
import ClassicEditor from '@ckeditor/ckeditor5-build-classic'
import EditQuestion from '../EditQuestion'
import AnswerRequest from '../layoutDialogs/AnswerRequest'
import {eventBus} from '../../main'

export default {
    name: 'Question',
    components: {
        appEditQuestion: EditQuestion, appAnswerRequest: AnswerRequest
    },
    props: {
        question: {
            type: Object,
            required: true,
        },
        blocked: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            editor: ClassicEditor,
            editQuestionDialog: false,
            editorData: '',
            expand: false,
            answerRequestDialog: false,
            timeout: 1000,
            timeoutToken: undefined,
            sendedShowUserCardEvent: false
        }
    },
    computed: {
        listItems() {
            return [
                {
                    title: 'edit',
                    clicked: this.editQuestion,
                    active: this.questionUser.id === this.loggedInUser.id && !this.blocked
                },
                {
                    title: 'delete',
                    clicked: this.deleteQuestion,
                    active: this.questionUser.id === this.loggedInUser.id && !this.blocked
                },
                {
                    title: 'report',
                    clicked: this.reportQuestion,
                    active: this.questionUser.id !== this.loggedInUser.id
                }
            ]
        },
        activeOptionsCount() {
            return this.listItems.filter(option => option.active).length
        },
        questionUser() {
            return this.question.user
        },
        loggedInUser() {
            return this.$store.getters.user
        }
    },
    methods: {
        sendShowUserCardEvent(event) {
            this.timeoutToken = setTimeout(() => {
                this.sendedShowUserCardEvent = true
                eventBus.$emit('showUserCard', {event, userId: this.question.user.id})
            }, this.timeout)
        },
        sendHideUserCardEvent(event) {
            if (!this.sendedShowUserCardEvent) {
                clearTimeout(this.timeoutToken)
            } else {
                eventBus.$emit('hideUserCard', event)
                this.sendedShowUserCardEvent = false
            }
        },
        deleteQuestion() {
            this.$store.dispatch('deleteQuestion', this.question.id)
        },
        answerRules() {
            if (this.editorData.length == 0) {
                this.$store.commit('SET_NOTIFICATION', {
                    message: 'answer musn\'t empty',
                    type: 'error'
                })
                return false
            }
            return true
        },
        addAnswer() {
            if (!this.answerRules()) return
            this.$store.dispatch('addAnswer', {
                editorContent: this.editorData,
                questionId: this.question.id
            })
                .then(response => {
                    this.expand = false;
                })

        },
        editQuestion() {
            this.editQuestionDialog = true
        },
        reportQuestion() {
            this.$store.dispatch('reportQuestion', {
                id: this.question.id,
                userId: this.question.user_id,
            })
        }
    }
}
</script>

