<template>
  <v-card>
    <v-card-title color="grey">
      Edit Question
    </v-card-title>

    <v-card-text>
      <p>remove or add topics</p>
      <v-chip-group mobile-break-point="150" column>
        <v-chip label text-color="white"
                color='indigo'
                close
                v-for="(item,index) in question.topics" :key="index"
                @click:close="deleteTopic()">
          {{ item.title }}
        </v-chip>
      </v-chip-group>
      <v-autocomplete
        v-model="question.topics"
        :search-input.sync="searchValue"
        item-text="title"
        item-value="id"
        :items="topicsFromSearch"
        chips
        multiple
        deletable-chips
        return-object
        hide-no-data
        hide-selected
        label="Add Topic"
        :loading="autoCompleteisLoading"
        @update:search-input="getAsyncData"
      >
      </v-autocomplete>
      <v-text-field
        v-model="body"
      ></v-text-field>
    </v-card-text>

    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn class="mr-3" @click="cancel">
        Cancel
      </v-btn>
      <v-btn class="mr-3" @click="edit">Edit</v-btn>
    </v-card-actions>

  </v-card>
</template>

<script>/* eslint-disable */
import {apiClient} from "../services/apiService";
import debounce from 'lodash/debounce'
  export default {
    props: {
      question: {
        type: Object,
        required:true
      },
    },
    data() {
      return {
        body: this.question.body,
        autoCompleteisLoading:false,
        topicsFromSearch:[],
        searchValue: '',
      }
    },
    methods: {
      edit() {
        this.question.body = this.body
        this.$store.dispatch('editQuestion',{question:this.question})
        .then(()=>{
          this.cancel()
        })
      },
      cancel() {
        this.$emit('cancel')
      },
      getAsyncData: debounce(function () {
        if (!!this.searchValue && this.searchValue.length > 2) {
          this.autoCompleteisLoading = true
          apiClient.get('/topics/search', {
            params: {
              searchValue: this.searchValue
            }
          })
            .then(response => {
              this.topicsFromSearch = response.data
              this.autoCompleteisLoading = false
            })
            .catch(() => {
              this.autoCompleteisLoading = false
            })
        }
      }, 1000)
    },
  }
</script>
