<template>
  <v-card flat class="color-grad-light" style="font-family: 'Lucida Console'">
    <v-card flat class="mb-12 pb-12" ref="navbar-container">
      <v-app-bar color="rgba(0,0,0,0.7)" dark class="px-md-12 nav-class" ref="navbar">

        <v-app-bar-nav-icon @click="drawer = !drawer"
                            class="hidden-sm-and-up">
        </v-app-bar-nav-icon>

        <v-toolbar-title>
          <v-card active-class="none" class="pa-2 px-4 transparent brand"
                  flat :to="{name:'home'}">
            QA
          </v-card>
        </v-toolbar-title>
        <v-divider vertical inset/>

        <v-menu transition="scale-transition" offset-y>

          <template v-slot:activator="{ on, attrs }">

            <v-btn class="btn-bar-hover" large
                   style="font-family: 'Lucida Console'"
                   v-bind="attrs"
                   v-on="on"
                   text
            >
              Notifications
            </v-btn>

          </template>

          <app-notifications/>

        </v-menu>

        <v-spacer/>
        <template v-for="(item,index) in items">
          <template v-if="item.active">
            <v-btn style="font-family: 'Lucida Console'"
                   large class="btn-bar-hover mr-md-8"
                   v-if="item.title === 'Ask'"
                   @click="addQuestionDialog=true"
                   :key="item.title" outlined color="pink"
                   :to="item.to">
              {{item.title}}
            </v-btn>
            <v-divider v-if="item.title === 'Register'" vertical inset :key="index"/>
            <v-btn style="font-family: 'Lucida Console'"
                   v-if="item.title !== 'Ask'" large class="btn-bar-hover"
                   :key="item.title" text :to="item.to">
              <!--                <v-avatar v-if="item.image!=null" size="30px" left>-->
              <!--                  <v-img  :src="item.image.url"></v-img>-->
              <!--                </v-avatar>-->
              {{item.title}}
            </v-btn>
          </template>
        </template>
        <app-user-option-menu :user="user" v-if="!!user"/>

      </v-app-bar>

    </v-card>

    <v-row class="justify-center ">
      <v-col cols="10">
        <router-view></router-view>
      </v-col>
    </v-row>

    <!--    add question dialog-->

    <v-dialog :fullscreen="$vuetify.breakpoint.smAndDown"
              :transition="$vuetify.breakpoint.smAndDown?'dialog-bottom-transition':'dialog-transition'"
              v-model="addQuestionDialog" width="600px">
      <app-add-question v-if="addQuestionDialog" @cancel="addQuestionDialog=false"/>
    </v-dialog>

    <v-navigation-drawer
      v-model="drawer" class="pt-12"
      absolute
      temporary
    >
      <v-card v-if="!!user" flat class="d-flex pa-0 justify-center">
        <v-card class="pa-0" flat>
          <v-card-text>
            <v-avatar size="100" class="mx-4" color="red">
              <v-img v-if="user.image!==null" :src="user.image.url"></v-img>
              <span v-else class="white--text" style="font-size: 200%">
                {{user.first_name.charAt(0).toUpperCase()}}
                {{user.last_name.charAt(0).toUpperCase()}}
              </span>
            </v-avatar>
          </v-card-text>
          <v-card-title>
            {{user.first_name}} {{user.last_name}}
          </v-card-title>
        </v-card>
      </v-card>

      <v-divider class="my-3"/>
      <v-list nav flat>

        <template v-for="item in items">
          <v-list-item v-if="item.active" link :key="item.title" :to="item.to">
            <v-list-item-icon>
              <v-icon>{{item.icon}}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{item.title}}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </template>

      </v-list>

    </v-navigation-drawer>
    <v-fab-transition>
      <v-btn
        v-if="this.$vuetify.breakpoint.smAndDown"
        color="pink"
        dark large
        fixed
        bottom
        right
        fab
        @click="addQuestionDialog=true"
      >
        Ask
      </v-btn>
    </v-fab-transition>
  </v-card>
</template>

<script>/* eslint-disable */
import Notifications from '../components/home/Notifications'
import AddQuestion from '../components/AddQuestion'
import UserOptionMenu from "../components/userOptionMenu";

export default {
    name: 'Layout',
    components: {
        appUserOptionMenu: UserOptionMenu,
        appAddQuestion: AddQuestion,
        appNotifications: Notifications
    },
    data() {
        return {
            askedQuestionBody: '',
            drawer: false,
            addQuestionDialog: false
        }
    },
    methods: {
        scrolled() {
            let that = this
            let container = that.$refs['navbar-container']
            let navbar = that.$refs['navbar']

            if (scrollY - 100 > container.height) {
                navbar.fixed = true
                container.height = 'fit-content'
                navbar.$el.classList.remove('nav-class')
                container.$el.classList.remove('navbar-container')
            }

        },
    },
    computed: {
        items() {
            return [
                {title: 'Ask', icon: 'mdi-calendar-question', active: this.$vuetify.breakpoint.mdAndUp},
                {title: 'Login', icon: '', to: {name: 'login'}, active: !this.isLoggedIn},
                {title: 'Register', icon: '', to: {name: 'register'}, active: !this.isLoggedIn},
            ]
        },
        isLoggedIn() {
            return this.$store.getters.isLoggedIn;
        },
        user() {
            return this.$store.getters.user
        },
    },
    created() {
        window.onscroll(this.scrolled);
    },
    mounted() {
        if (this.$route.name == 'home') {
            this.$refs['navbar-container'].height = '627'
            this.$refs['navbar-container'].$el.classList.add('navbar-container')
            this.$refs['navbar'].color = 'rgba(0,0,0,0.3)'
        }
    }

}

</script>

<style>
  .color-grad-light {
    /*background:url(../backgroundImage.svg) fixed !important;*/
  }

  .color-grad-light {
    background-color: #fbfcff !important;
  }

  /*.btn-bar-hover:hover{*/
  /*  border-bottom-style: solid;*/
  /*  border-bottom-color: red;*/
  /*}*/

  .brand {
    font-family: 'Lucida Calligraphy';
    font-size: xx-large;
    text-shadow: 0 -2px 5px #FFF,
    0 -4px 5px #AAA,
    0 -6px 5px #666,
    0 -8px 5px #000;
  }

  .navbar-container {
    background-image: url("../homebg.jpg");
    -webkit-background-size: cover;
    background-position: center;
    clip-path: ellipse(100% 60% at 50% 40%);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .nav-class {
    flex: none;
    position: sticky;
    top: 0;
  }

</style>
