<template>
  <ValidationObserver ref="obs" v-slot="{ invalid, validated, passes }">
    <v-row class="d-flex justify-center">
      <v-col cols="10" md="7">
        <v-card>
          <v-toolbar  flat dark color="green lighten-2">
            <v-toolbar-title class="text-center headline">Login</v-toolbar-title>
          </v-toolbar>
          <v-card-text class="px-8 px-md-12">
            <v-form>
              <VTextFieldWithValidation color="green" rules="required|email" v-model="email" label="E-mail"/>
              <VTextFieldWithValidation color="green" rules="required" type="password" label="Password" vid="password"
                                        v-model="password"/>
            </v-form>
          </v-card-text>

          <v-card-actions class="pa-8">

            <router-link style="text-decoration: none"
                         class="teal--text"
                         :to="{name:'email_reset_password'}"
            >
              Forget My Password
            </router-link>

            <v-spacer/>
            <!--        <v-btn @click="submit(validate)">Validate</v-btn>-->
            <v-btn color="green" outlined
                   :loading="isLoading"
                   :disabled="invalid || !validated"
                   @click="passes(submit)"
            >
              LogIn
            </v-btn>

          </v-card-actions>

          <v-card-actions class="px-8">
            <div class="green--text">
              If Dont Have An Account
              <router-link style="text-decoration: none"
                           class="teal--text"
                           :to="{name:'register'}"
              >SignUp
              </router-link>
              Now
            </div>
          </v-card-actions>

        </v-card>
      </v-col>
    </v-row>

  </ValidationObserver>
</template>

<script>/* eslint-disable */
import {ValidationObserver, ValidationProvider} from "vee-validate";
import VTextFieldWithValidation from '../../components/inputs/VTextFieldWithValidation';
import VSelectWithValidation from '../../components/inputs/VSelectWithValidation';

export default {
  data () {
    return {
      email: "",
      password: "",
      isLoading:false
    }
  },
  components: {
    ValidationObserver,
    ValidationProvider,
    VTextFieldWithValidation,
    VSelectWithValidation
  },
  methods: {
    async submit() {
      this.isLoading=true;
      this.$store.dispatch('login', {
        email: this.email, password: this.password
      })
        .then(response => {
          this.isLoading=false;
          this.$router.push({name: 'home'})
        })
        .catch(()=>{
          this.isLoading=false;
        })
    }
  }
};
</script>
