/* eslint-disable */
import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/auth/Login'
import Register from '../views/auth/Register'
import Home from '../views/Home'
import TopicPage from '../views/TopicPage'
import Logout from '../views/auth/Logout'
import store from '../store/index'
import QuestionAnswers from '../components/QuestionAnswers'
import ShowProfile from '../components/profile/ShowProfile'
import Admin from '../views/Admin.vue'
import Layout from '../views/Layout'
import Reports from '../components/adminDashboard/Reports'
import Charts from '../components/Charts'
import AccountSettings from "../components/profile/AccountSettings";
import ErrorPage from "../views/ErrorPage";
import EmailResetPassword from "../views/auth/EmailResetPassword";

Vue.use(VueRouter)

const routes = [
  {
    path: '',
    name: 'layout',
    component: Layout,
    children: [
      {
        path: '',
        name: 'home',
        component: Home,
      },

      {
        path: '/login',
        name: 'login',
        component: Login,
        meta: {
          requiredGuest: true
        }
      },

      {
        path: '/register',
        name: 'register',
        component: Register,
        meta: {
          requiredGuest: true
        }
      },

      {
        path: '/logout',
        name: 'logout',
        component: Logout,
        meta: {
          requiredAuth: true
        }
      },

      {
        path:'/email_reset_password',
        name:'email_reset_password',
        component:EmailResetPassword
      },

      {
        path: '/profile/:id',
        name: 'profile',
        component: ShowProfile,
        meta: {
          isQuestionAnswersUrl: true
        },
        beforeEnter(to, from, next) {
          if (store.getters.isLoggedIn && store.getters.user.id == to.params.id) {
            next({name: 'myProfile'})
          }
          next(true)
        }
      },

      {
        path: '/profile',
        name: 'myProfile',
        component: ShowProfile,
        meta: {
          isQuestionAnswersUrl: true,
          requiredAuth: true
        }
      },

      {
        path: '/Account_settings',
        name: 'accountSettings',
        component: AccountSettings,
        meta: {
          requiredAuth: true
        }
      },

      {
        path: '/topic/:id',
        name: 'topic',
        component: TopicPage
      },

      {
        path: '/question/:id',
        name: 'questionAnswers',
        component: QuestionAnswers,
        meta: {
          isQuestionAnswersUrl: true
        }
      },
    ]
  },
  {
    path: '/admin',
    name: 'admin',
    component: Admin,
    meta:
      {
        requiredAdmin: true,
      },
    children: [
      {
        path: '',
        name: 'dashboard',
        component: Charts
      },

      {
        path: 'reports',
        name: 'reports',
        component: Reports
      }
    ]
  },
  {
    path:'/error',
    name:'error-page',
    component:ErrorPage,
  },

]

const router = new VueRouter({
  routes,
  mode: 'history'
})

router.beforeEach((to, from, next) => {
  if (from.meta.isQuestionAnswersUrl) {
    store.commit('SET_FROM_QUESTION_ANSWERS_URL', true)
  } else {
    store.commit('SET_FROM_QUESTION_ANSWERS_URL', false)
  }

  if (to.matched.some(record => record.meta.requiredAuth)) {

    if (store.getters.isLoggedIn) {
      next(true)
    } else {
      next({name: 'login'})
    }

  }
  else if (to.matched.some(record => record.meta.requiredGuest)) {

    if (!store.getters.isLoggedIn) {
      next(true)
    } else {
      next('/')
    }

  }
  else {
    next(true)
  }
})

export default router
