<template>
  <v-card flat class="pa-0">
    <template v-for="(question,index) in userQuestions">
      <app-extended-question :blocked="blocked" :question="question" :key="index"/>
    </template>
  </v-card>
</template>

<script>/* eslint-disable */
import ExtendedQuestion from "../../home/ExtendedQuestion";
import {mapGetters} from 'vuex'

export default {
    name: "QuestionsTab",
    components: {
        appExtendedQuestion: ExtendedQuestion
    },
    props: {
        userId: {
            type: Number,
            required: true
        },
        blocked: {
            type: Boolean,
            default: false
        }
    },
    computed: mapGetters(['userQuestions']),
    watch: {
        userId(newValue, oldValue) {
            this.$store.dispatch('retrieveUserQuestions', newValue)
        }
    },
    created() {
        this.$store.dispatch('retrieveUserQuestions', this.userId)
    }
}
</script>

