  <v-card flat class="cyan lighten-5" height="100vh">
    <app-notification/>
    <v-row class="d-flex justify-center reports-table-container">
      <v-col cols="10">
        <v-card>

          <v-tabs v-model="tab" background-color="primary" dark show-arrows>
            <v-tab v-for="item in items" :key="item.tab">
              {{ item.title }}
            </v-tab>
          </v-tabs>

          <v-tabs-items v-model="tab">
            <v-tab-item
              v-for="(item,index) in reportsComponentsList" :key="index">
              <component :is="reportComponent" :reportsType="item.type" :reports="item.data"/>
            </v-tab-item>
          </v-tabs-items>

        </v-card>
      </v-col>
    </v-row>

