</template>

<script>/* eslint-disable */
import ReportsTable from './ReportsTable'
import Notification from '../Notification'
export default {
  components: {
    appReportsTable: ReportsTable,
    appNotification:Notification
  },
  computed: {
    questionsReports () {
      let questionsR = this.$store.getters.getQuestionsReports
      if (questionsR != null) {
        return questionsR
      } else {
        return []
      }
      // return this.$store.getters.getQuestionsReports
    },
    answersReports () {
      let answersR = this.$store.getters.getAnswersReports
      if (answersR != null) {
        return answersR
      } else {
        return []
      }
      // return this.$store.getters.getAnswersReports
    },
    reportsComponentsList () {
      return [
        {
          type: 'questionsReports',
          data: this.questionsReports
        },
        {
          type: 'answersReports',
          data: this.answersReports
        },
      ]
    }
  },
  data () {
    return {
      tab: null,
      reportComponent: ReportsTable,
      items: [
        {
          title: 'Reported Questions',
          icon: 'mdi-view-dashboard'
        },
        {
          title: 'Reported Answers',
          icon: 'mdi-help-box'
        },
      ],

    }
  },
  created () {
    this.$store.dispatch('retrieveReports')
  }
}
</script>

