<template>
  <v-card flat width="100%">
    <v-card-title class="justify-center primary white--text">
      Top Active User
    </v-card-title>
    <v-simple-table>
      <thead>
      <tr>
        <th>User Name</th>
        <th>Questions Count</th>
        <th>Answers Count</th>
        <th>Votes Count</th>
      </tr>
      </thead>
      <tbody>
        <tr v-for="(activeUser,index) in getTopActiveUsers" :key="index">
          <td>{{activeUser.first_name}} {{activeUser.last_name}}</td>
          <td>{{activeUser.questions_count}}</td>
          <td>{{activeUser.answers_count}}</td>
          <td>{{activeUser.votes_count}}</td>
        </tr>
      </tbody>
    </v-simple-table>
  </v-card>
</template>

<script>/* eslint-disable */
import {mapGetters} from 'vuex'
    export default {
        name: "TopActiveUsers",
        computed:mapGetters(["getTopActiveUsers"]),
        created() {
            this.$store.dispatch("retrieveTopActiveUsers")
        }
    }
</script>

