<template>
  <v-card :class="($vuetify.breakpoint.smAndDown)?'pa-5':''">
    <v-card>
      <v-card-title class="headline grey darken lighten-2 ">
        Report Details
        <v-row justify="end">
          <v-btn large icon @click="cancel">
            <v-icon x-large color="red">mdi-close</v-icon>
          </v-btn>
        </v-row>
      </v-card-title>
      <v-card-text class="mt-3">
        <p v-if="reportsType==='answersReports'" class="headline" v-html="report.question.body"></p>
        <v-card flat>
          <p :class="(reportsType==='questionsReports')?'headline':''" v-html="report.body"></p>
        </v-card>
      </v-card-text>
      <v-divider></v-divider>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-menu offset-y min-width="30vw" bottom
                origin="center center"
                transition="scale-transition">
          <template v-slot:activator="{ on, attrs }">
            <v-btn :disabled="isLoading" :color="(!isLoading)?'primary':'grey'" class="ml-auto font-weight-bold"
                   v-bind="attrs" v-on="on">
              <v-icon>mdi-steam-box</v-icon>
              options
            </v-btn>
          </template>
          <v-list color="grey lighten-2">
            <v-list-item>
              <v-btn color="error" text @click="deleteUser">
                <v-icon>mdi-account-remove</v-icon>
                <span v-if="reportsType==='questionsReports'">Delete the owner of this question</span>
                <span v-if="reportsType==='answersReports'">Delete the owner of this answer</span>
              </v-btn>
            </v-list-item>
            <v-list-item>
              <v-btn color="error" text @click="deleteQuestion">
                <v-icon>mdi-delete</v-icon>
                <span v-if="reportsType==='questionsReports'">Delete this question</span>
                <span v-if="reportsType==='answersReports'">Delete the original question</span>
              </v-btn>
            </v-list-item>
            <v-list-item v-if="reportsType==='answersReports'">
              <v-btn color="error" text @click="deleteAnswer">
                <v-icon>mdi-pencil-box-outline</v-icon>
                Delete this answer
              </v-btn>
            </v-list-item>
            <v-list-item>
              <v-btn color="error" text @click="ignoreReport">
                <v-icon>mdi-comment-remove-outline</v-icon>
                Ignore
              </v-btn>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-card-actions>
    </v-card>
  </v-card>
</template>

<script>/* eslint-disable */
export default {
  name: 'ReportsDetails',
  props: {
    report: {
      type: Object,
      required: true
    },
    reportsType: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      isLoading: false
    }
  },
  methods: {
    cancel () {
      this.$emit('cancel')
    },
    deleteQuestion () {
      this.isLoading = true
      let qId = this.report.id
      if (this.reportsType === 'answersReports') qId = this.report.question.id
      this.$store.dispatch('deleteQuestionByAdmin', {
        // reportId: this.report.report_id,
        questionId: qId,
        reportsType: this.reportsType,
        answerId: this.report.id
      })
        .then(response => {
          this.isLoading = false
          this.cancel()
        })
        .catch(error => {
          this.isLoading = false
          // this.cancel()
        })
    },
    deleteUser () {
      this.isLoading = true
      this.$store.dispatch('deleteUserByAdmin', {
        reportedContentId: this.report.id,
        reportsType: this.reportsType,
        userId: this.report.user_id
      })
        .then(response => {
          this.isLoading = false
          this.cancel()
        })
        .catch(error => {
          this.isLoading = false
          // this.cancel()
        })
    },
    deleteAnswer () {
      this.isLoading = true
      this.$store.dispatch('deleteAnswerByAdmin', {
        // reportId: this.report.report_id,
        answerId: this.report.id
      })
        .then(response => {
          this.isLoading = false
          this.cancel()
        })
        .catch(error => {
          this.isLoading = false
          // this.cancel()
        })
    },
    ignoreReport () {
      this.isLoading = true
      this.$store.dispatch('ignoreReports', {
        reportsType: this.reportsType,
        id: this.report.id
      })
        .then(response => {
          this.cancel()
          this.isLoading = false
        })
        .catch(error => {
          this.isLoading = false
          // this.cancel()
        })
    },
  },
}
</script>

