<template>
      <v-menu offset-y transition="scale-transition">
        <template v-slot:activator="{on}">
          <v-btn text class="transparent" v-on="on">
            {{userName.substr(0,15)}}
          </v-btn>
        </template>
        <v-list color="rgba(0,0,0,.8)" dark>
          <v-list-item v-for="(option,index) in options"
                       :key="index" exact :to="option.to"
          >
            <v-list-item-content>
              <v-list-item-title>
                {{option.title}}
              </v-list-item-title>
            </v-list-item-content>

          </v-list-item>
        </v-list>
      </v-menu>
</template>

<script>/* eslint-disable */
    export default {
        name: "userOptionMenu",
        props:{
            user:{
                type:Object,
                required:true
            }
        },
        computed: {
            userName(){
                return this.user.first_name+' '+this.user.last_name
            },
            options() {
                return [
                    {title:'Profile',to:{name:'myProfile'}},
                    {title: 'Account Setting',to:{name:'accountSettings'}},
                    {title: 'Logout',to:{name:'logout'}}
                ]
            }
        },

    }
</script>

<style scoped>

</style>
