/* eslint-disable */
import {apiClient} from "../services/apiService";

export default {
  actions: {
    addAnswer({commit}, {editorContent, questionId}) {
      return new Promise((resolve, reject) => {
        apiClient.post('answers', {
          editorContent,
          questionId
        })
          .then(response => {
            commit('ADD_ANSWER',response.data)
            commit('SET_NOTIFICATION', {message: 'Answer added successfully', type: 'success'})
            resolve()
          })
          .catch(error =>{
            reject()
          })
      })
    },
    editAnswer({commit}, {answer_id, answer_body}) {
      return new Promise((resolve, reject) => {
        apiClient.put(`answers/${answer_id}`, {
          answer_id,
          answer_body
        })
          .then(response => {
            commit('EDIT_ANSWER', response.data.answer)
            commit('EDIT_PROFILE_ANSWER', response.data.answer)
            commit('SET_NOTIFICATION', {message: 'answer updated successfully', type: 'success'})
            resolve()
          })
          .catch((error) => {
            commit('SET_NOTIFICATION', {
              message: error.response.data, type: 'error'
            })
            reject()
          })
      })
    },
    deleteAnswer({commit}, {answer}) {
      return new Promise((resolve, reject) => {
        apiClient.delete('answers/' + answer.id)
          .then(response => {
            commit('DELETE_ANSWER', answer)
            commit('DELETE_PROFILE_ANSWER', answer)
            commit('SET_NOTIFICATION', {
              message: response.data,
              type: 'success'
            })
            resolve()
          })
          .catch((error) => {
            commit('SET_NOTIFICATION', {
              message: error.response.data,
              type: 'error'
            })
            reject()
          })
      })
    },

  }
}
