<template>
  <v-card flat class="pa-0">
    <template v-for="(question,index) in userAnswers">
      <app-extended-question :blocked="blocked" :question="question"
                             :key="index"
      />
      <v-divider class="mb-8" :key="index"/>
    </template>
  </v-card>
</template>

<script>/* eslint-disable */
import {mapGetters} from 'vuex'
import ExtendedQuestion from "../../home/ExtendedQuestion";

export default {
    name: "AnswersTab",
    components: {
        appExtendedQuestion: ExtendedQuestion
    },
    props: {
        userId: {
            type: Number,
            required: true
        },
        blocked: {
            type: Boolean,
            default: false
        }
    },
    computed: mapGetters(['userAnswers']),
    watch: {
        userId(newValue, oldValue) {
            this.$store.dispatch('retrieveUserAnswers', newValue)
        }
    },
    created() {
        this.$store.dispatch('retrieveUserAnswers', this.userId)
    }
}
</script>

