<template>
  <v-card  flat width="100%" class="reports-table">
<!--    <v-card-title class="justify-center red white&#45;&#45;text">-->
<!--      Questions Reports-->
<!--    </v-card-title>-->
    <v-card-text v-if="reports.length===0">
      <h3 >There are no reports!</h3>
    </v-card-text>
    <v-simple-table v-else style="overflow-y: scroll; max-height: 70vh">
      <thead>
      <tr>
        <th class="question-section">question</th>
        <th>No of reports</th>
      </tr>
      </thead>
      <tbody>

      <v-dialog :fullscreen="$vuetify.breakpoint.smAndDown"
                persistent
                :transition="$vuetify.breakpoint.smAndDown?'dialog-bottom-transition':'dialog-transition'"
                v-model="showReportDetailsDialog" width="600px"

                >
        <app-report-details v-if="showReportDetailsDialog" :reports-type="reportsType" :report="selectedReport" @cancel="showReportDetailsDialog=false"/>
      </v-dialog>

      <tr v-for="(report,index) in reports" :key="index">

        <v-hover v-slot:default="{ hover }">
          <td class="question-body">
            <app-truncated-text class="" :text="report.body"/>
            <div style="{height: 100%}" v-if="hover">
              <v-btn color="blue lighten-4" class="btn-hover-state" @click="doThings(report)">See Details
              </v-btn>
            </div>
          </td>

        </v-hover>
        <td>{{report.reports_count}}</td>
      </tr>
      </tbody>
    </v-simple-table>
  </v-card>
</template>

<script>/* eslint-disable */
import TruncatedText from '../TruncatedText'
import ReportDetails from '../ReportDetails'

export default {
  name: 'ReportsTable',
  components: {
    appTruncatedText: TruncatedText,
    appReportDetails: ReportDetails
  },
  data () {
    return {
      showReportDetailsDialog: false,
      selectedReport:null
    }
  },
  props: {
    reports: {
      type: Array,
      required: true
    },
    reportsType: {
      type: String,
      required: true
    }
  },
  methods: {
    deleteQuestion () {
      this.$store.dispatch('deleteQuestionByAdmin', {
        reportId: this.report.report_id,
        questionId: this.report.id
      })
    },
    deleteUser () {
      this.$store.dispatch('deleteUserByAdmin', {
        reportId: this.report.report_id,
        userId: this.report.user_id
      })
    },
    deleteAnswer () {
      if (this.report.answer) {
        this.$store.dispatch('deleteAnswerByAdmin', {
          reportId: this.report.report_id,
          questionId: this.report.id,
          answerId: this.report.answer.id
        })
      }
    },
    ignoreReport(){
      this.$store.dispatch('ignoreReport', {
        // reportId: this.report.report_id,
        reportType: this.reportsType,
        id: this.report.id
      })
    },
    doThings(report){
      this.showReportDetailsDialog=true
      this.selectedReport=report
    }
  },
}
</script>

<style scoped>
  .question-section {
    width: 98%;
  }

  .question-body {
    position: relative;
    margin: 5px;
  }

  .btn-hover-state {
    position: absolute;
    display: block;
    top: 1%;
    left: 50%;
  }
</style>
