/* eslint-disable */
import {apiClient} from "../services/apiService";

export default {
  state: {
    userQuestions: [],
    userAnswers: [],
    userProfile: {}
  },
  getters: {
    userAnswers(state) {
      return state.userAnswers
    },
    userQuestions(state) {
      return state.userQuestions
    },
    userProfile(state) {
      return state.userProfile
    },
    userCredentials(state) {
      return state.userProfile.credentials
    },
    userShortTopics(state) {
      if (!state.userProfile.topics) return []
      return state.userProfile.topics.slice(0, 3)
    },
    userTopics(state) {
      return state.userProfile.topics
    },
    userTopicsTitles(state) {
      return state.userProfile.topics.map(topic => {
        return topic.title
      })
    },
    userTopicsValues(state) {
      return state.userProfile.topics.map(topic => {
        return 100 * topic.rank
      })
    },
    userShortCredentials(state) {
      let credentials = state.userProfile.credentials
      let arr = []
      if (!credentials) return []
      return credentials.filter(item => {
        if (!arr.find(arrItem => arrItem == item.type)) {
          arr.push(item.type)
          return true
        }
        return false
      })
    }
  },
  mutations: {
    RETRIEVE_USER_ANSWERS(state, answers) {
      state.userAnswers = answers
    },
    RETRIEVE_USER_QUESTIONS(state, questions) {
      state.userQuestions = questions
    },
    RETRIEVE_USER_PROFILE(state, userProfile) {
      state.userProfile = userProfile
    },
    DELETE_PROFILE_QUESTION(state, question_id) {
      let index = state.userQuestions.findIndex(item => item.id == question_id)
      if (index != -1)
        state.userQuestions.splice(index, 1)

      index = state.userAnswers.findIndex(item => item.id == question_id)
      if (index != -1)
        state.userAnswers.splice(index, 1)

    },
    EDIT_PROFILE_QUESTION(state, question) {
      let oldQuestion = state.userQuestions.find(item => item.id == question.id)

      if (!!oldQuestion) oldQuestion.body = question.body

      oldQuestion = state.userAnswers.find(item => item.id == question.id)

      if (!!oldQuestion) oldQuestion.body = question.body
    },
    DELETE_PROFILE_ANSWER(state, answer) {
      let questionId = answer.question_id

      let question = state.userQuestions.find(item => item.id == questionId)
      if (!!question) {
        let index = question.answers.findIndex(item => item.id === answer.id)
        if (index != -1) question.answers.splice(index, 1)
      }

      question = state.userAnswers.find(item => item.id == questionId)
      if (!!question) {
        let index = question.answers.findIndex(item => item.id === answer.id)
        if (index != -1) question.answers.splice(index, 1)
      }
    },
    EDIT_PROFILE_ANSWER(state, answer) {
      let questionId = answer.question_id

      let question = state.userQuestions.find(item => item.id === questionId)
      if (!!question) {
        let answerIndex = question.answers.findIndex(item => item.id === answer.id)
        if (answerIndex != -1) question.answers.splice(answerIndex, 1, answer)
      }

      question = state.userAnswers.find(item => item.id === questionId)
      if (!!question) {
        let answerIndex = question.answers.findIndex(item => item.id === answer.id)
        if (answerIndex != -1) question.answers.splice(answerIndex, 1, answer)
      }
    },
    PROFILE_ANSWER_VOTE(state, {answer, upVotesCount, downVotesCount, value}) {
      let questionId = answer.question_id

      let question = state.userQuestions.find(item => item.id === questionId)
      if(!!question) {
        let oldAnswer = question.answers.find(item => item.id === answer.id)
        if(!!oldAnswer) {
          oldAnswer.upVotesCount = upVotesCount
          oldAnswer.downVotesCount = downVotesCount

          if (oldAnswer.userReacted === value) {
            question.votes_count--
            oldAnswer.userReacted = 0
          } else {
            if (oldAnswer.userReacted == 0)
              question.votes_count++;
            oldAnswer.userReacted = value
          }
        }
      }

      question = state.userAnswers.find(item => item.id === questionId)
      if(!!question) {
        let oldAnswer = question.answers.find(item => item.id === answer.id)
        if(!!oldAnswer) {
          oldAnswer.upVotesCount = upVotesCount
          oldAnswer.downVotesCount = downVotesCount

          if (oldAnswer.userReacted === value) {
            question.votes_count--
            oldAnswer.userReacted = 0
          } else {
            if (oldAnswer.userReacted == 0)
              question.votes_count++;
            oldAnswer.userReacted = value
          }
        }
      }
    },
  },

  actions: {
    retrieveUserQuestions({commit}, userId) {
      apiClient.get('users/' + userId + '/questions')
        .then(response => {
          commit('RETRIEVE_USER_QUESTIONS', response.data.userQuestions)
        })
        .catch(error => {
          console.log(error)
        })
    },
    retrieveUserAnswers({commit}, userId) {
      apiClient.get('users/' + userId + '/answers')
        .then(response => {
          commit('RETRIEVE_USER_ANSWERS', response.data.userAnswers)
        })
        .catch(error => {
          console.log(error)
        })
    },
    retrieveUserProfile({commit}, userId) {
      return new Promise((resolve, reject) => {
        apiClient.get('users/' + userId)
          .then(response => {
            commit('RETRIEVE_USER_PROFILE', response.data)
            resolve(response.data)
          })
          .catch(error => {
            reject(error.response.data)
          })
      })
    }
  }
}
