<template>
  <v-card v-if="!!UnAnsweredQuestions" class="fixed-card my-2" flat hover>

    <v-carousel height="inherit" light
                vertical vertical-delimiters="true" cycle
                hide-delimiter-background show-arrows-on-hover
    >
      <v-toolbar-title class="d-flex justify-center white--text"
                       style="background-color: #bea38b"
      >
        Un Answerd Qusetions
      </v-toolbar-title>
      <v-carousel-item
        v-for="(item,index) in UnAnsweredQuestions"
        :to="{name:'questionAnswers',params:{id:item.id}}"
        :key="index"
      >
        <v-card flat hover height="300px"
                style="background-color: bisque;font-family: 'Lucida Handwriting'"
                class="d-flex align-center justify-center text-capitalize font-weight-bold"
        >
          {{item.body}}
        </v-card>
      </v-carousel-item>
    </v-carousel>
  </v-card>
</template>

<script>/* eslint-disable */
import {mapGetters} from 'vuex'

export default {
    name: "UnAnsweredQuestionsCarousel",
    computed: {
        ...mapGetters([
            'UnAnsweredQuestions'
        ]),
    },
    created() {
        this.$store.dispatch('retrieveUnAnsweredQuestions')
    }
}
</script>

