/* eslint-disable */
import {apiClient} from "../services/apiService";

export default {
  state: {
    questions: [],
    unAnsweredQuestions: [],
    minId: 0,
    maxId: 0
  },
  getters: {
    getAllQuestions(state) {
      return state.questions
    },
    getQuestion(state) {
      return (question_id) => {
        let question = state.questions.find(item => item.id === question_id)
        if (!question)
          question = state.unAnsweredQuestions.find(item => item.id === question_id)
        return question
      }
    },
    UnAnsweredQuestions(state) {
      return state.unAnsweredQuestions.length>0?state.unAnsweredQuestions:null;
    }
  },
  mutations: {
    DELETE_ANSWER(state, answer) {
      let questionId = answer.question_id

      let question = state.questions.find(item => item.id == questionId)

      if (!!question) {
        let index = question.answers.findIndex(item => item.id === answer.id)
        if (index != -1) question.answers.splice(index, 1)
      }

    },
    ADD_ANSWER(state,answer){
      let questionId = answer.question_id
      let question = state.questions.find(item => item.id === questionId)
      if (!!question) {
        question.answers.push(answer)
      }
    },
    EDIT_ANSWER(state, answer) {
      let questionId = answer.question_id

      let question = state.questions.find(item => item.id === questionId)

      if (!!question) {
        let answerIndex = question.answers.findIndex(item => item.id === answer.id)
        if (answerIndex != -1) question.answers.splice(answerIndex, 1, answer)
      }

    },
    DELETE_QUESTION(state, question_id) {

      let index = state.questions.findIndex(item => item.id == question_id)

      if (index != -1)
        state.questions.splice(index, 1)
    },
    EDIT_QUESTION(state, question) {
      let oldQuestion = state.questions.find(item => item.id == question.id)
      if (!!oldQuestion) oldQuestion.body = question.body;
    },
    CLEAR_QUESTIONS(state) {
      state.questions = []
      state.minId = 0
      state.maxId = 0
    },
    RETRIEVE_UN_ANSWERED_QUESTIONS(state, questions) {
      state.unAnsweredQuestions = questions;
    },
    RETRIEVE_ALL_QUESTIONS(state, data) {
      state.questions.push(...data.questions)
      state.minId = data.minId
      state.maxId = data.maxId
    },

    RETRIEVE_QUESTION(state, {answers, question, question_id}) {
      let localQuestion = state.questions.find(item => item.id == question_id)

      if (localQuestion == null) {
        state.questions.push(question)
      }

      localQuestion = state.questions.find(item => item.id == question_id)

      localQuestion.answers = answers;

    },

    ANSWER_VOTE(state, {answer, upVotesCount, downVotesCount, value}) {
      let questionId = answer.question_id
      let question = state.questions.find(item => item.id === questionId)
      if (!!question) {
        let oldAnswer = question.answers.find(item => item.id === answer.id)
        if (!!oldAnswer) {
          oldAnswer.upVotesCount = upVotesCount
          oldAnswer.downVotesCount = downVotesCount
          // answer1.votes_value= parseInt(answer1.votes_value) + value
          if (oldAnswer.userReacted === value) {
            question.votes_count--
            oldAnswer.userReacted = 0
          } else {
            if (oldAnswer.userReacted == 0)
              question.votes_count++;
            oldAnswer.userReacted = value
          }
        }
      }
    },
  },

  actions: {
    retrieveAllQuestions({commit, state}) {
      return new Promise((resolve, reject) => {
        apiClient.get('questions', {
          params: {
            minId: state.minId,
            maxId: state.maxId
          }
        })
          .then(response => {
            commit('RETRIEVE_ALL_QUESTIONS', response.data)
            resolve()
          })
          .catch(error => {
            console.log(error)
            reject(error)
          })
      })
    },
    retrieveQuestion({commit}, id) {
      apiClient.get('questions/' + id)
        .then(response => {

          commit('RETRIEVE_QUESTION', {
            question: response.data.question,
            answers: response.data.answers,
            question_id: id
          })

        })
        .catch(error => {
          console.log(error)
        })
    },
    answerVote({commit}, data) {
      apiClient.post('votes', {
        answerId: data.answer.id,
        value: data.value
      })
        .then(response => {
          commit('ANSWER_VOTE', {
            upVotesCount: response.data.upVotesCount,
            downVotesCount: response.data.downVotesCount,
            value: data.value,
            answer: data.answer
          })
          commit('PROFILE_ANSWER_VOTE', {
            upVotesCount: response.data.upVotesCount,
            downVotesCount: response.data.downVotesCount,
            value: data.value,
            answer: data.answer
          })
        })
        .catch(error => {
          console.log(error)
        })
    },
    addQuestion({commit}, question) {
      return new Promise((resolve, reject) => {
        apiClient.post('questions', question)
          .then(response => {
            resolve(response)
            console.log(response.data)
          })
          .catch(error => {
            console.log(error)
            reject(error)
          })
      })
    },
    retrieveUnAnsweredQuestions({commit, state}, question) {
      return new Promise((resolve, reject) => {
        if (state.unAnsweredQuestions.length == 0) {
          apiClient.get('unanswered_questions',)
            .then(response => {
              // console.log(response.data)
              commit('RETRIEVE_UN_ANSWERED_QUESTIONS', response.data)
              resolve(response)
            })
            .catch(error => {
              console.log(error)
              reject(error)
            })
        } else resolve(state.unAnsweredQuestions)
      })
    },
    editQuestion({commit}, {question}) {
      return new Promise((resolve, reject) => {
        apiClient.put('questions/' + question.id, question)
          .then(response => {
            commit('EDIT_QUESTION', response.data)
            commit('EDIT_PROFILE_QUESTION', response.data)
            commit('SET_NOTIFICATION',
              {message: 'Question Updated Successfully', type: 'success'})
            resolve(response)
          })
          .catch(error => {
            commit('SET_NOTIFICATION', {message: error.message, type: 'error'})
            reject(error)
          })
      })
    },
    deleteQuestion({commit}, question_id) {
      return new Promise((resolve, reject) => {
        apiClient.delete('questions/' + question_id)
          .then(response => {
            commit('DELETE_QUESTION', question_id)
            commit('DELETE_PROFILE_QUESTION', question_id)
            commit('SET_NOTIFICATION', {message: 'Question Deleted Successfully', type: 'success'})
            resolve(response)
          })
          .catch(error => {
            reject(error)
          })
      })
    },
    predictQuestionTopics({commit},questionBody){
      return new Promise((resolve,reject)=>{
        apiClient.post('/questions/predict',{
          text:questionBody
        })
          .then(response=>{
            resolve(response)
          })
          .catch(error=>{
            commit('SET_NOTIFICATION', {message: error.message, type: 'error'})
            reject(error)
          })
      })
    }
  }
}
