<template>
      <v-card v-if="!!topics" flat class="fixed-card" color="transparent">
          <v-list class="pa-0" color="transparent">
            <v-list-item class="add-border"
                         link v-for="item in topics"
                         :key="item.id"
                         :to="{name:'topic',params:{'id':item.id}}"
            >
              <v-avatar size="40" class="mr-4" color="grey">
                <v-img></v-img>
              </v-avatar>
              <v-list-item-content class="headline primary--text">
                <v-list-item-title>
                  {{item.title}}
                </v-list-item-title>
                <v-list-item-subtitle>
                  topics description
                </v-list-item-subtitle>
              </v-list-item-content>

            </v-list-item>
          </v-list>
      </v-card>
</template>

<script>/* eslint-disable */
    export default {
        name: "TopicList",
      computed: {
        topics() {
          return this.$store.getters.getTopics;
        }
      },

      created() {
          this.$store.dispatch('retrieveTopics');
      }
    }
</script>

<style scoped>
  .add-border{
    border-top-style: solid;
    border-width: 1px;
    border-color: #eee;
  }

</style>
