<template>
  <v-content >
    <v-row class="d-flex justify-space-around px-4" >
      <v-col cols="12" sm="10">
        <v-card loader-height="100%" height="80vh" :loading="questionsDetailsIsLoading" >
          <v-btn-toggle @change="getQuestionDetails" class="d-flex flex-wrap"
                        v-model="questionOption" dark mandatory tile>
            <v-btn active-class="primary" class="flex-grow-1"
                   v-for="(option,index) in options"
                   :value="option.id" :key="index"
            >
              {{option.title}}
            </v-btn>
          </v-btn-toggle>
          <v-card-text>
            <app-line-chart-container :chart-data="dataCollectionForQuestions"/>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="12" sm="10">
        <v-card loader-height="100%" height="80vh" :loading="answersDetailsIsLoading">
          <v-btn-toggle @change="getAnswersDetails" v-model="answerOption"
                        dark mandatory tile class="d-flex flex-wrap"
          >
            <v-btn active-class="primary" class="flex-grow-1" v-for="(option,index) in options"
                   :value="option.id" :key="index"
            >
              {{option.title}}
            </v-btn>
          </v-btn-toggle>
          <v-card-text>
            <app-line-chart-container :chart-data="dataCollectionForAnswers"/>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row class="justify-space-around px-md-9">
      <v-col cols="11" sm="7" md="4" >
        <app-top-contributers/>
      </v-col>
      <v-col cols="11" sm="11" md="7">
        <app-top-active-users/>
      </v-col>
    </v-row>

  </v-content>
</template>

<script>/* eslint-disable */
import LineChartContainer from './LineChartContainer'
import TopActiveUsers from './TopActiveUsers'
import TopContributors from './TopContributors'

export default {
    components: {
        appLineChartContainer: LineChartContainer,
        appTopActiveUsers: TopActiveUsers,
        appTopContributers: TopContributors
    },
    computed: {
        questionDetails() {
            let questionsRecords = this.$store.getters.getQuestionsDetails
            if (questionsRecords != null) {
                if (questionsRecords.find(item => item.id === this.questionOption) != null) {
                    let questions = questionsRecords.find(item => item.id === this.questionOption).questions
                    return {data: questions, labels: this.options.find(item => item.id === this.questionOption).labels}
                } else {
                    return {}
                }
            } else {
                return {}
            }

            if (questions != null) return questions
        },
        answersDetails() {
            let answersRecords = this.$store.getters.getAnswersDetails
            if (answersRecords != null) {
                if (answersRecords.find(item => item.id === this.answerOption) != null) {
                    let answers = answersRecords.find(item => item.id === this.answerOption).answers
                    return {data: answers, labels: this.options.find(item => item.id === this.answerOption).labels}
                } else {
                    return {}
                }
            } else {
                return {}
            }

            if (answers != null) return answers
        },
        dataCollectionForQuestions() {
            return {
                labels: this.questionDetails.labels,
                datasets: [
                    {
                        label: 'Questions Details',
                        backgroundColor: '#2ea0f8',
                        pointBackgroundColor: 'white',
                        borderWidth: 1,
                        pointBorderColor: '#249EBF',
                        data: this.questionDetails.data
                    }
                ]
            }
        },
        dataCollectionForAnswers() {
            return {
                labels: this.answersDetails.labels,
                datasets: [
                    {
                        label: 'Answers Details ',
                        backgroundColor: '#2ea0f8',
                        pointBackgroundColor: 'white',
                        borderWidth: 1,
                        pointBorderColor: '#249EBF',
                        data: this.answersDetails.data
                    }
                ]
            }
        }
    },
    data() {
        return {
            options: [],
            questionOption: '',
            questionChartData: {},
            answerOption: '',
            answerChartData: {},
            userReactivityOption: '',
            answersDetailsIsLoading: true,
            questionsDetailsIsLoading: true,
        }
    },
    methods: {
        getQuestionDetails() {
            this.questionsDetailsIsLoading = true
            this.$store.dispatch('retrieveQuestionsDetails', {
                option: {id: this.questionOption}
            })
                .then(() => {
                    this.questionsDetailsIsLoading = false
                })
        },
        getAnswersDetails() {
            this.answersDetailsIsLoading = true
            this.$store.dispatch('retrieveAnswersDetails', {
                option: {id: this.answerOption}
            })
                .then(() => {
                    this.answersDetailsIsLoading = false
                })
        }
    },
    created() {
        this.$store.dispatch('getOptions')
            .then((response) => {
                this.options = response.data
            })
        this.$store.dispatch('retrieveQuestionsDetails', {
            option: {id: 1}
        })
            .then(() => {
                this.questionsDetailsIsLoading = false
            })
        this.$store.dispatch('retrieveAnswersDetails', {
            option: {id: 1}
        })
            .then(() => {
                this.answersDetailsIsLoading = false
            })
    }
}
</script>

