<template>
  <v-card flat width="max-content" class="transparent mx-auto pa-1 my-auto justify-center">
    <v-card-title class="text-center green--text">
      Forget Your Password ?
    </v-card-title>
    <ValidationObserver v-slot="{invalid,validated,passes}">

      <v-card-text  class="py-8 mx-auto">
        <v-form>
          <v-text-field-with-validation
            v-model="email" outlined
            type="email" label="E-mail" rules="required|email"
          />
        </v-form>
      </v-card-text>

      <v-card-actions>
        <v-spacer/>
        <v-btn type="submit" @click="passes(submitForm)" :loading="sendRequest"
               :disabled="invalid || !validated" block color="green" outlined
        >
          Send
        </v-btn>
      </v-card-actions>

    </ValidationObserver>

  </v-card>

</template>

<script>/* eslint-disable */
import {apiClient} from "../../services/apiService";
import {ValidationObserver} from  "vee-validate"
import VTextFieldWithValidation from "../../components/inputs/VTextFieldWithValidation";

export default {
    name: "EmailResetPassword",
    components: {
        VTextFieldWithValidation, ValidationObserver
    },
    data() {
        return {
            email: '',
            sendRequest:false
        }
    },
    methods: {
        submitForm() {
            this.sendRequest = true
            apiClient.post('password/create', {email:this.email})
                .then(response=>{
                    this.$store.commit('SET_NOTIFICATION',{
                        type:'success',
                        message:response.data.message
                    })
                })
                .catch(error=>{
                    this.$store.commit('SET_NOTIFICATION',{
                        type:'error',
                        message:error.response.data.message
                    })
                })
                .finally(()=>{
                    this.sendRequest = false
                })
        }
    }
}
</script>

