<template>
  <v-card
    class="mx-auto"
    flat
  >
    <v-card-text v-html="answer.body" style="font-size:18px;"></v-card-text>
    <v-divider v-if="!pressedType" class="mx-10 mt-12"/>
    <v-card-actions class="px-md-12 px-sm-6">
      <span class="subtitle-2 grey--text">Answered By</span>
      <v-avatar class="ml-4" size="35" left>
        <v-img :src="answerUser.image.url"/>
      </v-avatar>
      {{answerUser.first_name}} {{answerUser.last_name}}
      <v-spacer/>

      <v-tooltip top>

        <template v-slot:activator="{ on }">
          <v-badge left overlap bordered :content="answer.upVotesCount">
            <v-btn v-on="on" class="mx-1"
                   small text :color="answer.is_user_reacted===1?'blue':''"
                   @click="vote(1)"
            >
              <v-icon>mdi-thumb-up</v-icon>
            </v-btn>
          </v-badge>
        </template>
        <span>Up Vote</span>
      </v-tooltip>
      <v-tooltip top>
        <template v-slot:activator="{ on }">
          <v-badge left overlap bordered :content="answer.downVotesCount">
            <v-btn small v-on="on" class="mx-1"
                   text :color="answer.is_user_reacted===-1?'blue':''"
                   @click="vote(-1)"
            >
              <v-icon>mdi-thumb-down</v-icon>
            </v-btn>
          </v-badge>
        </template>
        <span>Down Vote</span>
      </v-tooltip>

      <v-menu left top
              offset-y transition="scroll-x-reverse-transition"
              v-if="activeOptionsCount > 0"
      >
        <template v-slot:activator="{on}">
          <v-btn text small color="primary" v-on="on">
            ...
          </v-btn>
        </template>
        <v-list>
          <template v-for="(option,index) in answersOptions">
            <v-list-item :key="index"
                         v-if="option.isActive"
                         @click="option.action"
            >
              <v-list-item-action-text>
                {{option.title}}
              </v-list-item-action-text>
            </v-list-item>
          </template>
        </v-list>
      </v-menu>
    </v-card-actions>
    <v-divider v-if="pressedType" class="mx-md-10 mx-4"/>

    <v-expand-transition>
      <v-card v-if="expand" flat>
        <v-card-text>
          <ckeditor :editor="editor" v-model="editorData"/>
        </v-card-text>

        <v-card-actions>

          <v-spacer/>
          <v-btn @click="editAnswer"
                 :disabled="isDisabled"
                 outlined color="primary"
          >
            Update
          </v-btn>

        </v-card-actions>
      </v-card>
    </v-expand-transition>

  </v-card>

</template>

<script>/* eslint-disable */
import ClassicEditor from '@ckeditor/ckeditor5-build-classic'
import {eventBus} from "../../main";

export default {
    name: 'Answer',
    props: {
        answer: {
            type: Object,
            required: true
        },
        pressedType: {
            type: Boolean,
            default: false
        },
        blocked:{
            type:Boolean,
            default: false
        }
    },
    data() {
        return {
            expand: false,
            editorData: this.answer.body,
            editor: ClassicEditor,
            isDisabled: false
        }
    },
    computed: {

        answerUser() {
            return this.answer.user;
        },

        loggedInUser() {
            return this.$store.getters.user
        },

        isAuthorize() {
            return this.loggedInUser.id === this.answer.user_id
        },

        answersOptions() {
            return [
                {
                    title: 'delete',
                    action: this.sendDeleteEvent,
                    icon: '',
                    color: 'red',
                    isActive: !!this.loggedInUser && this.isAuthorize && !this.blocked
                },
                {
                    title: 'edit',
                    action: this.showEditPanel,
                    icon: '',
                    color: 'green',
                    isActive: !!this.loggedInUser && this.isAuthorize && !this.blocked
                },
                {
                    title: 'report',
                    action: this.reportAnswer1,
                    icon: '',
                    color: 'green',
                    isActive:!!this.loggedInUser && !this.isAuthorize
                }
            ];
        },
        activeOptionsCount(){
            return this.answersOptions.filter(option=>option.isActive).length
        }

    },

    methods: {
        showEditPanel() {
            this.expand = !this.expand;
        },
        sendDeleteEvent() {
            eventBus.$emit('showDeleteDialog', {
                title: 'delete answer',
                body: this.answer.body,
                fn: this.deleteAnswer
            });
        },
        deleteAnswer() {
            return this.$store.dispatch('deleteAnswer', {answer: this.answer})
        },
        answerRules() {
            if (this.editorData.length == 0) {
                this.$store.commit('SET_NOTIFICATION', {
                    message: 'answer musn\'t empty',
                    type: 'error'
                })
                return false
            }
            if (this.editorData.toString() == this.answer.body.toString()) {
                this.$store.commit('SET_NOTIFICATION', {
                    message: 'answer doesnt change',
                    type: 'error'
                })
                return false
            }
            return true
        },
        editAnswer() {
            if (!this.answerRules()) return
            this.isDisabled = true
            this.$store.dispatch('editAnswer', {
                answer_id: this.answer.id,
                answer_body: this.editorData
            })
                .then(() => {
                    this.expand = false
                    this.isDisabled = false
                })
                .catch(() => {
                    this.isDisabled = false
                })
        },

        reportAnswer1() {
            this.$store.dispatch('reportAnswer', {id: this.answer.id, userId: this.loggedInUser.id})
        },

        vote(value) {
            this.$store.dispatch('answerVote', {
                answer: this.answer,
                value: value
            })
        }

    },

}
</script>

