<template>
  <v-card flat>

    <v-card-title class="pa-0 d-flex flex-md-nowrap justify-center"
                  style="word-break: break-all;">

      <v-avatar left size="150" class="mx-4" color="red">
        <v-img v-if="userProfile.image!==null" :src="userProfile.image.url"/>
        <span v-else class="white--text" style="font-size: 200%">
          {{userProfile.first_name.charAt(0).toUpperCase()}}
          {{userProfile.last_name.charAt(0).toUpperCase()}}
        </span>
      </v-avatar>

      <div class="flex-grow-1 text-center text-md-left">
        {{userProfile.first_name}} {{userProfile.last_name}}
        <div class="subtitle-2 text--darken-1 grey--text">
          <div>{{userProfile.email}}</div>
          <span v-if="userProfile.description === '' && !editDescription">
            <v-btn text @click="editDescription = true">
              add description to your profile
            </v-btn>
          </span>

          <span v-if="userProfile.description !== '' && !editDescription">
            {{userProfile.description}}
            <v-btn icon @click="editDescription = true">
              edit
            </v-btn>
          </span>

          <v-text-field v-if="editDescription" @keydown.enter="editDescription = false"
                        label="Add Description"
                        v-model="userProfile.description"
          />

        </div>
      </div>

      <div class="d-flex flex-md-column">
        <div class="d-flex justify-center">
          <div class="align-self-center blue--text" style="font-size: 3rem">
            {{userProfile.points}}
          </div>
          <div>
            <v-rating x-small dense readonly style="height: 20px;"
                      :value="userProfile.points/20"/>
            <div class="subtitle-1 grey--text" style="height: fit-content">points</div>
          </div>
        </div>
        <div class="d-flex flex-wrap justify-center my-2 grey--text">
          <div class="px-2 my-2" style="border-left: grey solid 5px">
            <div class="subtitle-2">Questions</div>
            <div class="subtitle-2">{{userProfile.questions_count}}</div>
          </div>
          <div class="px-4 my-2" style="padding-left: 10px; border-left: grey solid 5px">
            <div class="subtitle-2">Answers</div>
            <div class="subtitle-2">{{userProfile.answers_count}}</div>
          </div>
        </div>
      </div>
    </v-card-title>
  </v-card>
</template>

<script>/* eslint-disable */
import {mapGetters} from 'vuex'

export default {
    name: "Description",
    props: {
        userId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            editDescription: false,
        }
    },
    computed: mapGetters(['userProfile']),
    watch: {
        userId(newValue, oldValue) {
            this.$store.dispatch('retrieveUserProfile', newValue)
        }
    },
    created() {
        this.$store.dispatch('retrieveUserProfile', this.userId)
    }
}
</script>

<style scoped>

</style>
